<template>
  <div class="bg">
    <div class="whitebg">
      <div style="display: flex; align-items:center; ">
        <img src="../assets/login_logo/bg-left.png" style="border-top-left-radius: 8px; border-bottom-left-radius: 8px;" />
      </div>
      <div
        style="display: flex; flex-direction: column; justify-content: center; height: 450px; margin-left: 80px; margin-right: 80px; width: 400px;"
      >
        <!--  -->
        <a-form-model
          v-if="switchLogin"
          key="login"
          ref="logForm"
          :rules="rules"
          :model="form"
          style="height: 600px;"
          >
          <h3 class="welcomelogo" style="margin-top: 80px; color: #13c2c2"></h3>

          <a-form-model-item ref="logusername" prop="logusername">
            <a-input class="input-text" v-model="form.logusername" placeholder="" type="text">
              <a-icon slot="prefix" type="user" :style="{ color: 'rgba(0,0,0,.25)'}"></a-icon>
            </a-input>
          </a-form-model-item>
          <a-form-model-item ref="logpassword" prop="logpassword">
            <a-input
              class="input-text"
              v-model="form.logpassword"
              autocomplete="false"
              placeholder=""
              type="password"
              @keyup.enter="handingClickBtn"
            >
              <a-icon type="lock" slot="prefix" :style="{ color: 'rgba(0,0,0,.25)'}"></a-icon>
            </a-input>
          </a-form-model-item>

          <a-form-model-item ref="checked" prop="checked" style="margin-top: -16px; margin-bottom: 5px;">
            <a-checkbox v-model="checked">15</a-checkbox>
            <!-- <a
              style="font-family: SourceHanSansCN-Regular; font-size: 14px; color: #3370ff; float: right;"
            >?</a> -->
          </a-form-model-item>

          <a-form-model-item>
            <async-button
              type="primary"
              ref="loginBtn"
              @click="cb=>cb(handleLogin())"
              class="login-button"
            ></async-button>
          </a-form-model-item>

          <div class="message-info">
            
            <a @click="switchReg" style="color: #27c5a5;"></a>
          </div>
        </a-form-model>

        <!--  -->
        <a-form-model
          v-else
          key="sign"
          ref="regForm"
          :model="regForm"
          :rules="regrules"
          style="height: 600px;"
        >
          <h3 class="welcomelogo" style="margin-top: 90px; margin-bottom: 24px; color: #13c2c2"></h3>

          <a-form-model-item has-feedback prop="regusername">
            <a-input
              class="input-text"
              ref="regusername"
              v-model="regForm.regusername"
              placeholder=""
              type="text"
            >
              <a-icon slot="prefix" type="user" :style="{ color: 'rgba(0,0,0,.25)'}"></a-icon>
            </a-input>
          </a-form-model-item>
          <a-form-model-item has-feedback prop="regpassword">
            <a-input
              class="input-text"
              v-model="regForm.regpassword"
              ref="regpassword"
              autocomplete="false"
              placeholder="6-12"
              type="password"
            >
              <a-icon type="lock" slot="prefix" :style="{ color: 'rgba(0,0,0,.25)'}"></a-icon>
            </a-input>
          </a-form-model-item>
          <a-form-model-item has-feedback prop="checkpassword">
            <a-input
              class="input-text"
              v-model="regForm.checkpassword"
              autocomplete="false"
              placeholder=""
              type="password"
              @keyup.enter="handingClickRegBtn"
            >
              <a-icon type="lock" slot="prefix" :style="{ color: 'rgba(0,0,0,.25)'}"></a-icon>
            </a-input>
          </a-form-model-item>

          <a-form-model-item>
            <async-button
              type="primary"
              ref="regBtn"
              @click="cb=>cb(handleRegister())"
              class="login-button"
            ></async-button>
          </a-form-model-item>
          <div class="message-info">
            <a @click="switchLog" style="color:#27c5a5;"></a>
          </div>
        </a-form-model>
      </div>
    </div>
  </div>
</template>

<script>
// import { client } from "../event/index.ts";
// import TitleBar from "@/components/TitleBar";
import { mapGetters, mapMutations } from "vuex";

import * as user from "@/request/user";

export default {
  data() {
    const validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        if (value.length > 5 && value.length < 12) {
          this.$refs.regForm.validateField("checkpassword");
          callback();
        } else {
          callback(new Error("612"));
        }
      }
    };
    const validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value != this.regForm.regpassword) {
        callback(new Error(""));
      } else {
        callback();
      }
    };

    return {
      switchLogin: true,
      checked: false,
      form: {
        logusername: "",
        logpassword: "",
      },
      rules: {
        logusername: [
          { required: true, message: "", trigger: "change" },
        ],
        logpassword: [
          { required: true, message: "", trigger: "change" },
        ],
      },
      regForm: {
        regusername: "",
        regpassword: "",
        checkpassword: "",
      },
      regrules: {
        regusername: [
          {
            validator: (rule, value, callback) => {
              if (value === "") {
                callback(new Error(""));
              } else {
                if (new RegExp("[\u4e00-\u9fa5]").test(value)) {
                  callback(new Error(""));
                } else {
                  if (value.length < 5) {
                    callback(new Error("5"));
                  } else {
                    new RegExp("^[a-zA-Z][a-zA-Z0-9_]{4,15}$").test(value)
                      ? callback()
                      : callback(new Error(""));
                  }
                }
              }
            },
            trigger: "change",
          },
        ],
        regpassword: [{ validator: validatePass, trigger: "change" }],
        checkpassword: [{ validator: validatePass2, trigger: "change" }],
      },
    };
  },

  methods: {
    ...mapGetters("userInfo", ["isLogin"]),
    ...mapMutations("userInfo", ["updateUserInfo", "shouldClearToken"]),
    handingClickRegBtn() {
      this.$refs.regBtn.click();
    },
    handleRegister() {
      return (
        this.$refs.regForm
          .validate()
          .then(() => {
            const { regusername, regpassword } = this.regForm;
            return user.register({
              username: regusername,
              password: regpassword,
            });
          })
          //2
          .then((...arg) => {
            return new Promise((res) =>
              setTimeout(() => {
                res(...arg);
              }, 500)
            );
          })
          .then(() => {
            this.$message.success("");
            this.switchLogin = true;
            if (this.$refs.logForm) {
              this.$refs.logForm.resetFields();
            }
            this.$refs.regusername = "";
            this.$refs.regpassword = "";
            this.$refs.checkpassword = "";
            if (this.$refs.regForm) {
              this.$refs.regForm.resetFields();
            }
          })
          .catch((msg) => {
            if (msg === false) console.warn("validate error"); // fasle 
          })
      );
    },
    handingClickBtn() 
    {
      this.$refs.loginBtn.click();
    },
    handleLogin() {
      // const checkedBool = this.checked;
      return (
        this.$refs.logForm
          .validate()
          .then(() => {
            const checked = Number(this.checked);
            // const checkedBool = this.checked;
            const { logusername, logpassword } = this.form;
            return user.login({
              username: logusername,
              password: logpassword,
              checked,
            });
          })
          //  2  loading
          .then((...arg) => {
            return new Promise((res) =>
              setTimeout(() => {
                res(...arg);
              }, 2000)
            );
          })
          .then(({ token, userInfo }) => {
            this.$router.push("/");
            this.updateUserInfo({
              token: token,
              username: userInfo.nickname,
              avatar: "blank",
              org: userInfo.depName,
            })
          })
          .catch((msg) => {
            if (msg === false) console.warn("validate error"); // fasle 
          })
      );
    },
    switchReg() {
      this.switchLogin = false;
      this.$refs.logForm.resetFields();
    },
    switchLog() {
      this.$refs.regForm.resetFields();
      this.switchLogin = true;
    },
  },
};
</script>

