const str = `




TDI



12L
2L0.5L



55
6-7k4-5k2k

`.split('').filter(v => ['', '', '\n'].indexOf(v) < 0).join('')

const rand = (min, max) => Math.floor(Math.random() * (max - min)) + min

export default (min = 10, max = 20) => new Array(rand(min, max))
    .fill(0)
    .map(() => str[rand(0, str.length)])
    .join('')

